.mainPage {
  display: flex;
  flex-direction: column;
  justify-content: center;
  width: 100%;
  height: 100%;
  padding: 0px 64px;

  &__label {
    display: flex;
    flex-direction: column;
    padding-bottom: 60px;

    h2 {
      font-weight: 700;
      font-size: 70px;
      line-height: 66px;
      color: #121212;
      strong {
        color: #0ec261;
      }

      @media screen and (max-width: 480px) {
        font-size: 32px;
        line-height: 100%;
        padding: 0px 16px;
      }
    }

    p {
      padding-top: 34px;
      font-weight: 300;
      font-size: 26px;
      line-height: 30px;
      color: #999999;

      @media screen and (max-width: 480px) {
        padding: 16px 16px 0px 16px;
        font-size: 18px;
        line-height: 21px;
      }
    }
  }

  @media screen and (max-width: 1024px) {
    padding: 0px 32px;
  }

  @media screen and (max-width: 320px) {
    padding: 0px;
  }
}

.reserveBtn {
  width: 250px;
  height: 48px;
  background: linear-gradient(90deg, #0ec261 2.61%, #039f67 112.6%);
  background-blend-mode: darken;
  border-radius: 8px;
  border: none;
  cursor: pointer;
  font-weight: 500;
  font-size: 18px;
  line-height: 21px;
  color: #ffffff;
  text-align: center;

  @media screen and (max-width: 320px) {
    width: 100%;
    border-radius: 0px;
  }
}
